.subscribe-container {
  background: #f9fafb;
  border: 1px solid #e6e6e6;
  border-radius: 4px;
  box-shadow: 0 2px 15px 0 rgba(210, 214, 220, 0.3);
  display: flex;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 2rem;
  max-width: 600px;
  padding: 1.25rem 1rem;
  width: 100%;
}
.subscribe-container, .subscribe-container * {
  box-sizing: border-box;
}
.subscribe-container form {
  margin-bottom: 0;
}
.subscribe-container__title {
  color: #000;
  display: inline-block;
  font-size: 1.75rem;
  line-height: 1.2;
  margin-bottom: 0.5rem;
}
.subscribe-container__subtitle {
  font-size: 1rem;
  color: #777;
  padding-bottom: 0;
  margin-block-start: 0;
  margin-bottom: 1rem;
}
.subscribe-container__email {
  border-color: #e6e6e6;
  border-radius: 4px;
  color: black;
  font-weight: 400;
  width: 100%;
  background: #fff;
  font-size: 15px;
  padding: 12px;
  border: 1px solid #e6e6e6;
  flex: 1 0 auto;
  line-height: 1.4;
  margin: 0;
  transition: border-color 0.3s ease-out;
  overflow: visible;
  margin-bottom: 1rem;
}
.subscribe-container__submit {
  background-color: #4caf50;
  border-radius: 8px;
  color: #fff;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: 700;
  transition: background-color 0.3s ease-in-out;
  padding: 12px 18px;
  width: 100%;
}
.subscribe-container__submit:hover {
  background-color: #3d8b40;
}